<template>
  <div class="container">
    <!--  -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title=""></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <div class="chat-item"
        v-for="(item,idx) in list"
        :key="item.timestamp + idx"
        :class="item.name==='xz' ? 'left':'right'"
      >
        <!-- templatedom -->
        <!-- div -->
        <template v-if="item.name==='xz'">
          <van-image fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
          <div class="chat-pao">{{item.msg}}</div>
        </template>
        <template v-else>
          <div class="chat-pao">{{item.msg}}</div>
          <van-image fit="cover" round :src="$store.state.userInfo.photo"/>
        </template>
      </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field v-model.trim="word" placeholder="...">
        <span  @click="send()" slot="button" style="font-size:12px;color:#999"></span>
      </van-field>
    </div>
  </div>
</template>

<script>
// websocket
import io from 'socket.io-client'
export default {
  name: 'UserChat',
  data () {
    return {
      // 
      list: [
        { name: 'xz', msg: '', timestamp: Date.now() },
        { name: 'me', msg: ', ', timestamp: Date.now() },
        { name: 'xz', msg: '', timestamp: Date.now() },
        { name: 'xz', msg: '', timestamp: Date.now() },
        { name: 'xz', msg: '', timestamp: Date.now() },
        { name: 'me', msg: ', ', timestamp: Date.now() },
        { name: 'xz', msg: '', timestamp: Date.now() },
        { name: 'xz', msg: '', timestamp: Date.now() }
      ],
      word: ''
    }
  },
  created () {
    // 1. webscoket
    // io(url, )
    // http://47.114.163.79:3003
    // http://ttapi.research.itcast.cn
    this.socket = io('http://47.114.163.79:3003', {
      query: {
        token: this.$store.state.tokenInfo.token
      }
    })
    this.socket.on('connect', () => {
      console.log('webscoket')
      // list
      this.list.push({
        msg: '',
        timestamp: Date.now(),
        name: 'xz'
      })
      this.$nextTick(() => {
        document.querySelector('.chat-list').scrollTop = document.querySelector('.chat-list').scrollHeight
      })
    })
    this.socket.on('message', obj => {
      console.log('', obj)
      // list
      this.list.push({
        msg: obj.msg,
        timestamp: obj.timestamp,
        name: 'xz'
      })
      this.$nextTick(() => {
        document.querySelector('.chat-list').scrollTop = document.querySelector('.chat-list').scrollHeight
      })
    })
  },
  methods: {
    // 
    send () {
      console.log(this.word)
      if (this.word === '') {
        return
      }
      if (this.socket) {
        // 1. websocket
        const info = {
          msg: this.word,
          timestamp: Date.now()
        }
        this.socket.emit('message', info)

        // 2. list
        //  ---- div ---- 
        this.list.push({
          name: 'me',
          msg: this.word,
          timestamp: Date.now()
        })
        // console.log(document.querySelector('.chat-list').scrollHeight)
        // setTimeout(() => {
        //   console.log(document.querySelector('.chat-list').scrollHeight)
        // }, 1000)
        this.$nextTick(() => {
          document.querySelector('.chat-list').scrollTop = document.querySelector('.chat-list').scrollHeight
        })
        // 3. 
        this.word = ''
      }
    }
  },
  destroyed () {
    // 
    if (this.socket) {
      this.socket.close()
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background:#fafafa;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item{
      padding: 10px;
      .van-image{
        vertical-align: top;
        width: 40px;
        height: 40px;
      }
      .chat-pao{
        vertical-align: top;
        display: inline-block;
        min-width: 40px;
        max-width: 70%;
        min-height: 40px;
        line-height: 38px;
        border: 0.5px solid #c2d9ea;
        border-radius: 4px;
        position: relative;
        padding: 0 10px;
        background-color: #e0effb;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before{
          content: "";
          width: 10px;
          height: 10px;
          position: absolute;
          top: 12px;
          border-top:0.5px solid #c2d9ea;
          border-right:0.5px solid #c2d9ea;
          background: #e0effb;
        }
      }
    }
  }
}
.chat-item.right{
  text-align: right;
  .chat-pao{
    margin-left: 0;
    margin-right: 15px;
    &::before{
      right: -6px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left{
  text-align: left;
  .chat-pao{
    margin-left: 15px;
    margin-right: 0;
    &::before{
      left: -5px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
